<template>
  <div id='divPaiCorrecao' width="100%" height="100%" >    
    <SideBar  :btnClickHandler="this.addMarcacao" nome="teste" />
    <Marcador tipo="GERAL" :actExcluir="excluir" :id="'marc_'+ item" :numero="item" :initialX="iniX" :initialY="iniY" 
        :initialW="tamH" :initialH="tamW" v-for="item in listMarc" :key="item"/>
    <b-img id="idImgCorrecao" v-bind:src="this.arquivo" fluid   /> 
    <div 
    style="height: 100%; width: 100%;"
      v-on:mousedown="pegaPosIni"
      v-on:mousemove="pegaPosFim"
      v-on:mouseup="soltar"
    > 
      <canvas id="UgCanvas" width="100%" height="100%" style="opacity:0.5;z-index: 100;transform: translateY(-100%);"/>	    
      
    </div>       
  </div>
</template>
<script>
import Marcador from "../components/Marcador"
import SideBar from "../components/SideBar"
import axios from 'axios'
import config from './../js/baseJs'

export default {  
  name: "Correcao",
  data ( ) {
    return {
      listMarc: [],
      numero: 0,
      arquivo: '',
      iniX:0,
      iniY:0,
      fimX:0,
      fimY:0,
      tamW:0,
      tamH:0,
      aux:0,
      desenhar: false    
    }
  },
  components: {
    Marcador,
    SideBar
  },
  mounted () { 
    //document.getElementById('divPaiCorrecao').style.display = "none";
    this.buscarCorrecao(this.$route.params.idCorrecao)
    var ctx = document.getElementById("UgCanvas");
		ctx.width  = window.innerWidth;
		ctx.height = window.innerHeight;  
  },
  methods: {
    pegaPosIni: function(e){      
      this.iniX = e.clientX
		  this.iniY = e.clientY-(document.getElementById('navEntreLinhas').offsetHeight)+document.documentElement.scrollTop
      this.desenhar = true
      var c=document.getElementById("UgCanvas");
    	var ctx=c.getContext("2d");
      ctx.fillStyle="rgb(60, 179, 113)"
    },
    pegaPosFim: function(e){           
      if(this.desenhar){
        // console.log('oi') 
        this.fimX = e.clientX
        this.fimY = e.clientY-(document.getElementById('navEntreLinhas').offsetHeight)+document.documentElement.scrollTop
      
        if(this.fimX > this.iniX){
          this.tamH = this.fimX - this.iniX			
        }else{
          // console.log(`a) ${this.iniX} -  ${this.iniY} -  ${this.fimX} - ${this.fimY}`)
          this.tamH = this.iniX - this.fimX
          this.aux = this.fimX
          this.fimX = this.iniX
          this.iniX = this.aux			
        }			
        if(this.fimY > this.iniY){
          this.tamW = this.fimY - this.iniY	
        }else{
          // console.log(`b) ${this.iniX} -  ${this.iniY} -  ${this.fimX} - ${this.fimX}`)
          this.tamW = this.iniY - this.fimY
          this.aux = this.fimY
          this.fimY = this.iniY
          this.iniY = this.aux
        }					
        // console.log(`fim 2) ${this.iniX} -  ${this.iniY} -  ${this.fimX} - ${this.fimX}`)
        // console.log( `posição ${this.iniX} <-> ${this.iniY}     tamanho = ${this.tamH} <-> ${this.tamW}` )	
        var c=document.getElementById("UgCanvas");
      	var ctx=c.getContext("2d");
        ctx.fillRect(this.iniX,this.iniY,this.tamH,this.tamW);	
        //this.onResize(this.iniX, this.iniY, this.tamW, this.tamW)
	  	}
    },
    soltar: function(){
      this.desenhar = false
      // console.log( `oia ${this.iniX} <-> ${this.iniY}     tamanho = ${this.tamH} <-> ${this.tamW}` )	      
      let posX = this.iniX
      
      var c=document.getElementById("UgCanvas");
	    var ctx=c.getContext("2d");
      ctx.clearRect(0, 0, c.width, c.height);   
      let vm = this 

      axios.post(process.env.ROOT_API.concat('/setmarcador'), {
          "x":posX,
          "y":vm.iniY,
          "width":vm.tamH,
          "height":vm.tamW,
          "marcacao_item":2,
          "id_correcao":vm.$route.params.idCorrecao          
      }).then(dados => {
        // console.log(dados.data.id)
        vm.listMarc.push(dados.data.id)
      }).catch(err => {
        console.log(err)
      })
    },
    addMarcacao: function (  ) {
      let vm = this
      //this.numero += 1
      //this.listMarc.push( this.numero )   
      axios.post(process.env.ROOT_API.concat('/setmarcador'), {
          "x":100,
          "y":10,
          "width":120,
          "height":50,
          "marcacao_item":2,
          "id_correcao":vm.$route.params.idCorrecao          
      }).then(dados => {
        // console.log(dados.data.id)
        this.listMarc.push(dados.data.id)
      }).catch(err => {
        console.log(err)
      })
    },
    excluir: function () {
      console.log( "alex" )
    },
    buscarCorrecao: function(aID) {        
      let loader = this.$loading.show(config.getLoaderProperties(this.$refs.formContainer))    
      var vm = this      
      axios.get(process.env.ROOT_API.concat('/dadoscorrecao'),{
        params: {
          id: aID
        }
      }).then(result => {        
        vm.arquivo =  result.data.dados.arquivo                  
        axios.get(process.env.ROOT_API.concat('/getmarcadores'),{
          params: {
            id_correcao: aID
          }
        }).then(result => {   
          let dados = result.data.dados       
          for(let i = 0;i <= dados.length;i++){   
            vm.listMarc.push(dados[i].id)
            let ctx = document.getElementById("UgCanvas");        
            ctx.height = document.getElementById('idImgCorrecao').height;  
          }
          document.getElementById('divPaiCorrecao').style.display = "block";
          loader.hide()
        }).catch(err => {
          loader.hide()
          document.getElementById('divPaiCorrecao').style.display = "block";
          console.log(err)
        })
      }).catch(err=>{
        document.getElementById('divPaiCorrecao').style.display = "block";
        loader.hide()
        alert(err)
      })
    }
  },  
  
}
</script>



