<template>
  <div>
    <!-- <b-img src="https://picsum.photos/1024/400/?image=41" alt="Responsive image" />
    <div 
    style="height: 100%; width: 100%;"
      v-on:mousedown="pegaPosIni"
      v-on:mousemove="pegaPosFim"
      v-on:mouseup="soltar"
    > 
      <canvas id="UgCanvas" width="100%" height="100%" style="opacity:0.5;z-index: 1000;transform: translateY(-94%);"/>	    
      
    </div> -->
    <b-button v-on:click="teste">teste</b-button>
  </div>
</template>
<script>
import VueDraggableResizable from "vue-draggable-resizable"
import axios from 'axios'
import config from './../js/baseJs'

export default {
  name: "Home",

  data: function () {
    return {
      width: 0,
      height: 0,
      x: 0,
      y: 0,
      iniX:0,
      iniY:0,
      fimX:0,
      fimY:0,
      tamW:0,
      tamH:0,
      aux:0,
      desenhar: false      
    }
  },
  mounted () {    
    var ctx = document.getElementById("UgCanvas");
		ctx.width  = window.innerWidth;
		ctx.height = window.innerHeight;    
  },
  methods: {
    pegaPosIni: function(e){      
      this.iniX = e.clientX
		  this.iniY = e.clientY+(document.getElementById('navEntreLinhas').offsetHeight*3.5)
      this.desenhar = true
      var c=document.getElementById("UgCanvas");
    	var ctx=c.getContext("2d");
      ctx.fillStyle="rgb(60, 179, 113)"
    },
    pegaPosFim: function(e){           
      if(this.desenhar){
        // console.log('oi') 
        this.fimX = e.clientX
        this.fimY = e.clientY+(document.getElementById('navEntreLinhas').offsetHeight*3.5)
      
        if(this.fimX > this.iniX){
          this.tamH = this.fimX - this.iniX			
        }else{
          // console.log(`a) ${this.iniX} -  ${this.iniY} -  ${this.fimX} - ${this.fimY}`)
          this.tamH = this.iniX - this.fimX
          this.aux = this.fimX
          this.fimX = this.iniX
          this.iniX = this.aux			
        }			
        if(this.fimY > this.iniY){
          this.tamW = this.fimY - this.iniY	
        }else{
          // console.log(`b) ${this.iniX} -  ${this.iniY} -  ${this.fimX} - ${this.fimX}`)
          this.tamW = this.iniY - this.fimY
          this.aux = this.fimY
          this.fimY = this.iniY
          this.iniY = this.aux
        }					
        // console.log(`fim 2) ${this.iniX} -  ${this.iniY} -  ${this.fimX} - ${this.fimX}`)
        // console.log( `posição ${this.iniX} <-> ${this.iniY}     tamanho = ${this.tamH} <-> ${this.tamW}` )	
        var c=document.getElementById("UgCanvas");
      	var ctx=c.getContext("2d");
        ctx.fillRect(this.iniX,this.iniY,this.tamH,this.tamW);	
        //this.onResize(this.iniX, this.iniY, this.tamW, this.tamW)
	  	}
    },
    soltar: function(){
      this.desenhar = false
      console.log( `oia ${this.iniX} <-> ${this.iniY}     tamanho = ${this.tamH} <-> ${this.tamW}` )	
      this.onResize(this.iniX, this.iniY, this.tamW, this.tamW)  
      
      var c=document.getElementById("UgCanvas");
	    var ctx=c.getContext("2d");
      ctx.clearRect(0, 0, c.width, c.height);    
    },
    onResize: function (x, y, width, height) {
      this.x = x
      this.y = y
      this.width = width
      this.height = height
    },
    teste: function(){
      let loader = this.$loading.show(config.getLoaderProperties(this.$refs.formContainer))
      setTimeout(function(){
        loader.hide()
      },2000)
    },
    
  }
}
</script>



